import _ from 'lodash'
import React from 'react'

import { useFieldProps } from './useFieldProps'
import { withField } from './withField'
import {
  ControllerListItem,
  ControllerListProps,
  WithWidthProps,
} from '../types'
import {
  FormControl,
  FormHelperText,
  InputLabel,
  MenuItem,
  Select as MUISelect,
} from '../../core'

const ContollerSelect = (
  props: ControllerListProps<WithWidthProps & { noneItemText?: string }>,
) => {
  const {
    label,
    field,
    helperText,
    items,
    disabled,
    hideNullOption,
    hasError,
    errorMsg,
    minWidth,
    fullWidth,
    noneItemText = 'None',
  } = useFieldProps(props)
  const helperTextOrErrorMsg = hasError ? errorMsg : helperText
  const _items = _.map(items, (item: ControllerListItem) =>
    item.track ? item : { track: item.value, ...item },
  )
  return (
    <FormControl
      margin='normal'
      error={hasError}
      disabled={disabled}
      style={{ minWidth: minWidth }}
      fullWidth={fullWidth}
    >
      <InputLabel htmlFor={field.name}>{label}</InputLabel>
      <MUISelect {...field}>
        {!hideNullOption && (
          <MenuItem value={''}>
            <em>{noneItemText}</em>
          </MenuItem>
        )}
        {items &&
          _.map(_items, item => (
            <MenuItem
              key={item.track}
              value={item.value}
              disabled={item.disabled}
            >
              {item.label}
            </MenuItem>
          ))}
      </MUISelect>
      <FormHelperText>{helperTextOrErrorMsg}</FormHelperText>
    </FormControl>
  )
}

export const Select = withField(ContollerSelect)
