import React, { ChangeEvent } from 'react'
import _ from 'lodash'
import {
  FormHelperText,
  FormControl,
  FormLabel,
  FormGroup,
  FormControlLabel,
  Checkbox as MUICheckbox,
} from '../../core'

import { withField } from './withField'
import {
  ControllerListProps,
  ControllerListItem,
  CheckboxProps,
  ControllerFieldProps,
  WithBasicColor,
} from '../types'
import { useFieldProps } from './useFieldProps'

const ControllerCheckBox = (
  props: ControllerFieldProps<CheckboxProps & WithBasicColor>,
) => {
  const {
    label,
    field,
    form,
    helperText,
    labelPlacement,
    icon,
    checkedIcon,
    hasError,
    errorMsg,
    color,
    blurHandler,
  } = useFieldProps(props)
  const helperTextOrErrorMsg = hasError ? errorMsg : helperText
  return (
    <FormControl margin='normal' error={hasError} component='fieldset'>
      <FormGroup>
        {
          <FormControlLabel
            control={
              <MUICheckbox
                icon={icon}
                color={color}
                checkedIcon={checkedIcon}
                checked={field.value}
                onChange={() => form.setFieldValue(field.name, !field.value)}
                onBlur={blurHandler}
              />
            }
            label={label}
            labelPlacement={labelPlacement}
          />
        }
      </FormGroup>
      <FormHelperText>{helperTextOrErrorMsg}</FormHelperText>
    </FormControl>
  )
}
export const Checkbox = withField(ControllerCheckBox)

const ControllerCheckBoxList = (
  props: ControllerListProps<CheckboxProps & WithBasicColor>,
) => {
  const {
    label,
    field,
    form,
    helperText,
    items,
    inline,
    icon,
    checkedIcon,
    hasError,
    color,
    errorMsg,
    blurHandler,
  } = useFieldProps(props)
  const helperTextOrErrorMsg = hasError ? errorMsg : helperText

  const isChecked = (item: any) => {
    return (
      form.values[field.name] &&
      form.values[field.name].indexOf(item.value) > -1
    )
  }
  const _items = _.map(items, (item: ControllerListItem) =>
    item.track
      ? item
      : { track: item.value, ...item, checked: isChecked(item) },
  )
  const handleChange = (item: any) => (e: ChangeEvent) => {
    let nl: any[]
    if (item.checked) {
      nl = _.chain(form.values[field.name])
        .filter(i => i !== item.value)
        .value()
    } else {
      if (form.values[field.name]) {
        nl = form.values[field.name]
        nl.push(item.value)
      } else {
        nl = [item.value]
      }
    }
    form.setFieldValue(field.name, nl)
  }
  return (
    <FormControl
      margin='normal'
      error={hasError}
      component='fieldset'
      onBlurCapture={blurHandler}
    >
      <FormLabel component='legend'>{label}</FormLabel>
      <FormGroup row={inline}>
        {_items &&
          _.map(_items, (item: ControllerListItem & { checked: boolean }) => (
            <FormControlLabel
              key={item.track}
              label={item.label}
              control={
                <MUICheckbox
                  icon={icon}
                  checkedIcon={checkedIcon}
                  onChange={handleChange(item)}
                  value={item.track}
                  checked={item.checked}
                  disabled={item.disabled}
                  color={color}
                />
              }
            />
          ))}
      </FormGroup>
      <FormHelperText>{helperTextOrErrorMsg}</FormHelperText>
    </FormControl>
  )
}
export const CheckboxList = withField(ControllerCheckBoxList)
