import { useContext } from '../context/'
import { SwitchTheme } from '../context/constraints'
import { ThemeInfo } from '../context/types'

export const useSwitchTheme = () => {
  const [{ config }, dispatcher] = useContext()
  const switchTheme = () => {
    dispatcher(SwitchTheme())
  }
  return [config.theme, switchTheme] as [ThemeInfo, () => void]
}
