import _ from 'lodash'

import { Reducer, ContextActions, Config, ThemeName } from './types'

export const config: Reducer<Config> = (state, action) => {
  switch (action.type) {
    case ContextActions.CHANGE_THEME:
      return _.assign(state, {
        theme: {
          name: action.theme as ThemeName,
          isDarkTheme: (action.theme as ThemeName) === 'dark',
          isLightTheme: (action.theme as ThemeName) === 'light',
        },
      })
    case ContextActions.SWITCH_THEME:
      const theme = state.theme.isDarkTheme ? 'light' : 'dark'

      return _.assign(state, {
        theme: {
          name: theme,
          isDarkTheme: theme === 'dark',
          isLightTheme: theme === 'light',
        },
      })
    default:
      return state
  }
}
