import { useState } from 'react'

export const useMouseHover = () => {
  const [mouseHover, setMouseHover] = useState(false)
  const handlers = {
    onMouseOver: () => {
      if (!mouseHover) {
        setMouseHover(true)
      }
    },
    onMouseLeave: () => {
      setMouseHover(false)
    },
  }
  type HandlersType = typeof handlers
  return [mouseHover, handlers] as [boolean, HandlersType]
}
