import React, { CSSProperties, HTMLAttributes } from 'react'
import _ from 'lodash'
import clsx from 'clsx'
import Select from 'react-select'
import { BaseTextFieldProps } from '@material-ui/core/TextField'
import { emphasize } from '@material-ui/core/styles'

import { Cancel as CancelIcon } from '../../icons'
import { ControllerListProps } from '../types'
import { withField } from './withField'
import { useFieldProps } from './useFieldProps'
import { useTheme } from '../../styles'
import {
  Typography,
  Paper,
  TextField,
  Chip,
  MenuItem,
  createStyles,
  makeStyles,
  Theme,
} from '../../core'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      flexGrow: 1,
      height: 250,
    },
    input: {
      display: 'flex',
      padding: 0,
      height: 'auto',
    },
    valueContainer: {
      display: 'flex',
      flexWrap: 'wrap',
      flex: 1,
      alignItems: 'center',
      overflow: 'hidden',
    },
    chip: {
      margin: theme.spacing(0.5, 0.25),
    },
    chipFocused: {
      backgroundColor: emphasize(
        theme.palette.type === 'light'
          ? theme.palette.grey[300]
          : theme.palette.grey[700],
        0.08,
      ),
    },
    noOptionsMessage: {
      padding: theme.spacing(1, 2),
    },
    singleValue: {
      fontSize: 16,
    },
    placeholder: {
      position: 'absolute',
      left: 2,
      bottom: 6,
      fontSize: 16,
    },
    paper: {
      position: 'absolute',
      zIndex: 1,
      marginTop: theme.spacing(1),
      left: 0,
      right: 0,
    },
    divider: {
      height: theme.spacing(2),
    },
  }),
)

function NoOptionsMessage(props: any) {
  return (
    <Typography
      color='textSecondary'
      className={props.selectProps.classes.noOptionsMessage}
      {...props.innerProps}
    >
      {props.children}
    </Typography>
  )
}

type InputComponentProps = Pick<BaseTextFieldProps, 'inputRef'> &
  HTMLAttributes<HTMLDivElement>

function inputComponent({ inputRef, ...props }: InputComponentProps) {
  return <div ref={inputRef} {...props} />
}

function Control(props: any) {
  const {
    children,
    innerProps,
    innerRef,
    selectProps: { classes, TextFieldProps },
  } = props

  return (
    <TextField
      InputProps={{
        inputComponent,
        inputProps: {
          className: classes.input,
          ref: innerRef,
          children,
          ...innerProps,
        },
      }}
      margin='normal'
      variant='standard'
      fullWidth
      {...TextFieldProps}
    />
  )
}

function Option(props: any) {
  return (
    <MenuItem
      ref={props.innerRef}
      selected={props.isFocused}
      component='div'
      style={{
        fontWeight: props.isSelected ? 500 : 400,
      }}
      {...props.innerProps}
    >
      {props.children}
    </MenuItem>
  )
}

function Placeholder(props: any) {
  return (
    <Typography
      color='textSecondary'
      className={props.selectProps.classes.placeholder}
      {...props.innerProps}
    >
      {props.children}
    </Typography>
  )
}

function SingleValue(props: any) {
  return (
    <Typography
      className={props.selectProps.classes.singleValue}
      {...props.innerProps}
    >
      {props.children}
    </Typography>
  )
}

function ValueContainer(props: any) {
  return (
    <div className={props.selectProps.classes.valueContainer}>
      {props.children}
    </div>
  )
}

function MultiValue(props: any) {
  return (
    <Chip
      tabIndex={-1}
      label={props.children}
      className={clsx(props.selectProps.classes.chip, {
        [props.selectProps.classes.chipFocused]: props.isFocused,
      })}
      onDelete={props.removeProps.onClick}
      deleteIcon={<CancelIcon {...props.removeProps} />}
    />
  )
}

function Menu(props: any) {
  return (
    <Paper
      square
      className={props.selectProps.classes.paper}
      {...props.innerProps}
    >
      {props.children}
    </Paper>
  )
}

const components = {
  Control,
  Menu,
  MultiValue,
  NoOptionsMessage,
  Option,
  Placeholder,
  SingleValue,
  ValueContainer,
}

const ReactSelectWithMUITextField = (
  props: ControllerListProps<{ multiselect?: boolean }>,
) => {
  const {
    placeholder,
    multiselect = false,
    label,
    field,
    form,
    items,
    disabled,
    helperText,
    hasError,
    errorMsg,
  } = useFieldProps(props)
  const classes = useStyles({})
  const theme = useTheme() as Theme
  const labelOrErrorMsg = hasError ? errorMsg : label

  const handleChange = (value: any) => {
    if (multiselect) {
      form.setFieldValue(
        field.name,
        value ? _.map(value, item => item.value) : [],
      )
    } else {
      form.setFieldValue(field.name, value ? value.value : null)
    }
  }

  const handleBlur = () => {
    form.setFieldTouched(field.name, true)
  }

  const selectStyles = {
    input: (base: CSSProperties) => ({
      ...base,
      color: theme.palette.text.primary,
      '& input': {
        font: 'inherit',
      },
    }),
  }
  let _value
  if (field.value) {
    if (multiselect)
      _value = _.filter(items, item => field.value.indexOf(item.value) > -1)
    else _value = _.find(items, item => item.value === field.value)
  } else {
    _value = multiselect ? [] : null
  }
  return (
    <Select
      classes={classes}
      styles={selectStyles}
      options={items}
      components={components}
      inputId={field.name}
      onBlur={handleBlur}
      value={_value}
      onChange={handleChange}
      isMulti={multiselect}
      isDisabled={disabled}
      placeholder={placeholder}
      TextFieldProps={{
        label: labelOrErrorMsg,
        InputLabelProps: {
          htmlFor: field.name,
          shrink: true,
        },
        error: hasError,
        helperText,
      }}
    />
  )
}

export const Autocomplete = withField(ReactSelectWithMUITextField)
