import React from 'react'

import { Formik, Form, FormikProps, Paper, PageProps } from '../../lib/'
import { Typography, Button, Grid } from '../../lib/core'

export const DisplayFormValues = (props: FormikProps<Object>) => (
  <>
    <Typography variant='h4'>Form Values</Typography>
    <Typography variant='subtitle1'>Values:</Typography>
    <code>
      <div>{JSON.stringify(props.values)}</div>
    </code>
    <br />
    <Typography variant='subtitle1'>Errors:</Typography>
    <code>{JSON.stringify(props.errors)}</code>
    <br />
    <Typography variant='subtitle1'>Touched:</Typography>
    <code>{JSON.stringify(props.touched)}</code>
  </>
)

export const withShowCaseForm = (
  initialValues: Object,
  schema: any,
  Fields: React.FC<any>,
) => ({ navigate }: PageProps) => {
  return (
    <Paper>
      <Formik
        initialValues={initialValues}
        onSubmit={() => {}}
        validationSchema={schema}
        render={(props: FormikProps<Object>) => {
          return (
            <Form>
              <Grid container>
                <Grid item md={6}>
                  <Fields />
                  <Grid
                    container
                    direction='row-reverse'
                    spacing={2}
                    style={{ marginTop: 22, paddingRight: 22 }}
                  >
                    <Grid item>
                      <Button
                        variant='contained'
                        color='primary'
                        onClick={() => props.submitForm()}
                      >
                        Save
                      </Button>
                    </Grid>
                    <Grid item>
                      <Button variant='outlined' color='primary'>
                        Option
                      </Button>
                    </Grid>
                    <Grid item>
                      <Button
                        variant='outlined'
                        color='secondary'
                        onClick={() => (navigate as any)('../')}
                      >
                        Go Back
                      </Button>
                    </Grid>
                    <Grid item>
                      <Button
                        variant='contained'
                        disabled
                        onClick={() => props.submitForm()}
                      >
                        Disabled
                      </Button>
                    </Grid>
                  </Grid>
                </Grid>
                <Grid item md={6}>
                  <DisplayFormValues {...props} />
                </Grid>
              </Grid>
            </Form>
          )
        }}
      />
    </Paper>
  )
}
