import React, { ChangeEvent } from 'react'
import _ from 'lodash'
import { ControllerListItem, ControllerListProps } from '../types'
import { useFieldProps } from './useFieldProps'
import { withField } from './withField'
import {
  FormControl,
  FormControlLabel,
  FormHelperText,
  FormLabel,
  Radio as MUIRadio,
  RadioGroup as MUIRadioGroup,
} from '../../core'

const ControllerRadioButtonList = (props: ControllerListProps<{}>) => {
  const {
    label,
    field,
    form,
    helperText,
    items,
    inline,
    hasError,
    errorMsg,
    labelPlacement,
    blurHandler,
  } = useFieldProps(props)
  const helperTextOrErrorMsg = hasError ? errorMsg : helperText

  const isChecked = (item: any) => {
    return form.values[field.name] && form.values[field.name] === item.value
  }
  const _items = _.map(items, (item: ControllerListItem) =>
    item.track
      ? item
      : { track: item.value, ...item, checked: isChecked(item) },
  )
  const handleChange = (item: any) => (e: ChangeEvent) => {
    form.setFieldValue(field.name, item.value)
  }
  return (
    <FormControl margin='normal' error={hasError} component='fieldset'>
      <FormLabel component='legend'>{label}</FormLabel>
      <MUIRadioGroup row={inline} onBlur={blurHandler}>
        {_items &&
          _.map(_items, (item: ControllerListItem & { checked: boolean }) => (
            <FormControlLabel
              key={item.track}
              label={item.label}
              labelPlacement={labelPlacement}
              control={
                <MUIRadio
                  onChange={handleChange(item)}
                  value={item.track}
                  checked={item.checked}
                  disabled={item.disabled}
                />
              }
            />
          ))}
      </MUIRadioGroup>
      <FormHelperText>{helperTextOrErrorMsg}</FormHelperText>
    </FormControl>
  )
}
export const RadioButton = withField(ControllerRadioButtonList)
