import React from 'react'
import * as Yup from 'yup'

import { withShowCaseForm } from './withShowCaseForm'

import { TextField } from '../../lib'
import { Typography, FormGroup } from '../../lib/core'

const schema = Yup.object().shape({
  name: Yup.string().required(),
  number: Yup.string().required(),
  datetime: Yup.string().required(),
  date: Yup.string().required(),
  time: Yup.string().required(),
  textarea: Yup.string().required(),
  textareaUnlimited: Yup.string().required(),
})

const initialValues = {
  name: '',
  number: '',
  datetime: '',
  date: '',
  time: '',
  textarea: '',
  textareaUnlimited: '',
}

export default withShowCaseForm(initialValues, schema, () => (
  <>
    <Typography variant='h4'>TextField</Typography>
    <FormGroup row>
      <TextField
        name='name'
        placeholder='Placeholder'
        label='Label'
        helperText='Helper text'
      />
      <TextField
        name='number'
        label='Number'
        type='number'
        helperText='Helper text'
      />
    </FormGroup>
    <TextField
      name='datetime'
      label='Date and Time'
      type='datetime-local'
      InputLabelProps={{
        shrink: true,
      }}
      helperText='Helper text'
    />
    <TextField
      name='date'
      label='Date'
      type='date'
      InputLabelProps={{
        shrink: true,
      }}
      helperText='Helper text'
    />
    <TextField
      name='time'
      label='Time'
      type='time'
      InputLabelProps={{
        shrink: true,
      }}
      helperText='Helper text'
    />
    <FormGroup row>
      <TextField
        name='textarea'
        label='Textarea (5 lines hight limit)'
        type='text'
        rowsMax={5}
        fullWidth
        multiline
        helperText='Helper text'
      />
    </FormGroup>
    <FormGroup row>
      <TextField
        name='textareaUnlimited'
        label='Textarea (unlimited)'
        type='text'
        fullWidth
        multiline
        helperText='Helper text'
      />
    </FormGroup>
  </>
))
