import React from 'react'
import clsx from 'clsx'

import { ThemeSwitcher } from '../ThemeSwitcher'
import { Menu as MenuIcon, ChevronLeft as ChevronLeftIcon } from '../../icons'
import {
  AppBar,
  IconButton,
  Drawer,
  makeStyles,
  Theme,
  Divider,
  Box,
  Typography,
  Toolbar,
} from '../../core'
import { useMouseHover } from '../..'

const drawerWidth = 240

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    display: 'flex',
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: 36,
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: 'nowrap',
  },
  drawerOpen: {
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerClose: {
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: 'hidden',
    width: theme.spacing(7),
  },
  toolbar: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
}))

type AppBarProps = React.PropsWithChildren<{
  AfterTitleSlot?: React.FC
  MenuSlot?: React.FC<{ open: boolean }>
  title: string
  hideThemeSwitch?: boolean
}>
export function Layout({
  children,
  AfterTitleSlot,
  MenuSlot,
  title,
  hideThemeSwitch = false,
}: AppBarProps) {
  const classes = useStyles()
  const [open_, setOpen] = React.useState(false)
  const [mouseHoverLeftMenu, mouseHoverLeftMenuHandler] = useMouseHover()
  const open = open_ || mouseHoverLeftMenu
  const handleDrawerOpen = () => {
    setOpen(true)
  }
  const handleDrawerClose = () => {
    setOpen(false)
  }
  return (
    <div className={classes.root}>
      <AppBar
        position='fixed'
        color='default'
        className={clsx(classes.appBar, {
          [classes.appBarShift]: open,
        })}
      >
        <Toolbar variant='dense'>
          {MenuSlot && !open && (
            <IconButton
              edge='start'
              color='inherit'
              aria-label='Menu'
              onClick={handleDrawerOpen}
            >
              <MenuIcon />
            </IconButton>
          )}
          <Typography variant='h6' color='inherit' noWrap>
            {title}
          </Typography>
          {AfterTitleSlot && <AfterTitleSlot />}
          {!hideThemeSwitch && (
            <Box
              component='span'
              ml='auto'
              style={{ float: 'left', display: 'flex' }}
            >
              <ThemeSwitcher />
            </Box>
          )}
        </Toolbar>
      </AppBar>
      {MenuSlot && (
        <Drawer
          variant='permanent'
          open={open}
          {...mouseHoverLeftMenuHandler}
          className={clsx(classes.drawer, {
            [classes.drawerOpen]: open,
            [classes.drawerClose]: !open,
          })}
          classes={{
            paper: clsx({
              [classes.drawerOpen]: open,
              [classes.drawerClose]: !open,
            }),
          }}
        >
          <div className={classes.toolbar}>
            <IconButton onClick={handleDrawerClose}>
              <ChevronLeftIcon />
            </IconButton>
          </div>
          <Divider />
          <MenuSlot open={open} />
        </Drawer>
      )}
      <main className={classes.content}>
        <div className={classes.toolbar} />
        {children}
      </main>
    </div>
  )
}
