import React, { ReactElement } from 'react'

import {
  MenuItem as MuiMenuItem,
  MenuList,
  ListItemIcon,
  Typography,
} from '../../core'

export const LateralMenu = ({ children }: React.PropsWithChildren<{}>) => (
  <MenuList style={{ display: 'flex', flexDirection: 'column' }}>
    {children}
  </MenuList>
)

export const MenuItem = ({
  label,
  icon,
  noWrap = false,
  disabled = false,
  onClick,
}: {
  label: string
  icon?: ReactElement<any>
  noWrap?: boolean
  disabled?: boolean
  onClick?: () => void
}) => (
  <MuiMenuItem onClick={onClick && onClick} disabled={disabled}>
    {icon && <ListItemIcon>{icon}</ListItemIcon>}
    <Typography variant='inherit' noWrap={noWrap}>
      {label}
    </Typography>
  </MuiMenuItem>
)
