import React, { ChangeEvent } from 'react'
import _ from 'lodash'

import { useFieldProps } from './useFieldProps'
import { withField } from './withField'
import {
  ControllerFieldProps,
  ControllerListItem,
  ControllerListProps,
  WithBasicColor,
} from '../types'
import {
  FormControl,
  FormControlLabel,
  FormGroup,
  FormHelperText,
  FormLabel,
  Switch as MUISwitch,
} from '../../core'

const ControllerSwitch = (props: ControllerFieldProps<WithBasicColor>) => {
  const {
    label,
    field,
    form,
    helperText,
    errorMsg,
    hasError,
    color,
    blurHandler,
  } = useFieldProps(props)
  const helperTextOrErrorMsg = hasError ? errorMsg : helperText
  return (
    <FormControl margin='normal' error={hasError} component='fieldset'>
      <FormGroup>
        {
          <FormControlLabel
            control={
              <MUISwitch
                onBlur={blurHandler}
                checked={field.value}
                color={color}
                onChange={() => form.setFieldValue(field.name, !field.value)}
              />
            }
            label={label}
          />
        }
      </FormGroup>
      <FormHelperText>{helperTextOrErrorMsg}</FormHelperText>
    </FormControl>
  )
}
export const Switch = withField(ControllerSwitch)

const ControllerSwitchList = (props: ControllerListProps<WithBasicColor>) => {
  const {
    label,
    field,
    form,
    helperText,
    items,
    inline,
    hasError,
    errorMsg,
    color,
    blurHandler,
  } = useFieldProps(props)
  const helperTextOrErrorMsg = hasError ? errorMsg : helperText

  const isChecked = (item: any) => {
    return (
      form.values[field.name] &&
      form.values[field.name].indexOf(item.value) > -1
    )
  }
  const _items = _.map(items, (item: ControllerListItem) =>
    item.track
      ? item
      : { track: item.value, ...item, checked: isChecked(item) },
  )
  const handleChange = (item: any) => (e: ChangeEvent) => {
    let nl: any[]
    if (item.checked) {
      nl = _.chain(form.values[field.name])
        .filter(i => i !== item.value)
        .value()
    } else {
      if (form.values[field.name]) {
        nl = form.values[field.name]
        nl.push(item.value)
      } else {
        nl = [item.value]
      }
    }
    form.setFieldValue(field.name, nl)
  }
  return (
    <FormControl margin='normal' error={hasError} component='fieldset'>
      <FormLabel component='legend'>{label}</FormLabel>
      <FormGroup row={inline} onBlur={blurHandler}>
        {_items &&
          _.map(_items, (item: ControllerListItem & { checked: boolean }) => (
            <FormControlLabel
              key={item.track}
              label={item.label}
              control={
                <MUISwitch
                  onChange={handleChange(item)}
                  value={item.track}
                  checked={item.checked}
                  disabled={item.disabled}
                  color={color}
                />
              }
            />
          ))}
      </FormGroup>
      <FormHelperText>{helperTextOrErrorMsg}</FormHelperText>
    </FormControl>
  )
}
export const SwitchList = withField(ControllerSwitchList)
