import React from 'react'
import * as Yup from 'yup'

import { withShowCaseForm } from './withShowCaseForm'
import LIST_OF_ITEMS from './ListOfItems'

import { Select, Autocomplete } from '../../lib/'
import { FormGroup, Grid, Typography } from '../../lib/core'

const schema = Yup.object().shape({
  simpleSelect: Yup.string().required(),
  autocomplete: Yup.string().required(),
  autocompleteMulti: Yup.array().required(),
})

const initialValues = {
  simpleSelect: '',
  autocomplete: '',
  autocompleteMulti: [],
}

export default withShowCaseForm(initialValues, schema, () => (
  <>
    <Typography variant='h4'>Selections</Typography>
    <FormGroup row>
      <Select
        name='simpleSelect'
        helperText='Helper text'
        label='Simple Select'
        minWidth='150px'
        items={LIST_OF_ITEMS}
      />
    </FormGroup>
    <FormGroup row>
      <Grid item md={11}>
        <Autocomplete
          name='autocomplete'
          label='Autocomplete'
          helperText='Helper text'
          placeholder='Placeholder'
          items={LIST_OF_ITEMS}
        />
      </Grid>
    </FormGroup>
    <FormGroup>
      <Grid item md={11}>
        <Autocomplete
          multiselect
          name='autocompleteMulti'
          label='Autocomplete Multiselect'
          helperText='Helper text'
          placeholder='Placeholder'
          items={LIST_OF_ITEMS}
        />
      </Grid>
    </FormGroup>
  </>
))
