import React, {
  createContext,
  useContext as useReactContext,
  useReducer,
} from 'react'
import { combineReducers } from './combineReducers'
import { config } from './config'
import { Action, Context, ContextProviderType } from './types'

export const StateContext = createContext({})

const reducers = combineReducers({
  config,
})

export const ContextProvider: ContextProviderType = ({
  children,
  initialState,
}) => {
  return React.createElement(
    StateContext.Provider,
    { value: useReducer(reducers, initialState) },
    children,
  )
}

export const useContext = () => {
  return (useReactContext(StateContext) as unknown) as [
    Context,
    React.Dispatch<Action>
  ]
}

export const useDispatcher = () => {
  const [, dispatcher] = useContext()
  return dispatcher
}
