import React from 'react'
import * as Yup from 'yup'

import LIST_OF_ITEMS from './ListOfItems'
import { withShowCaseForm } from './withShowCaseForm'

import { RadioButton } from '../../lib'
import { FormGroup, Typography } from '../../lib/core'

const schema = Yup.object().shape({
  radioButton: Yup.string().required(),
  radioButtonInline: Yup.string().required(),
})

const initialValues = {
  radioButton: '',
  radioButtonInline: '',
}

export default withShowCaseForm(initialValues, schema, () => (
  <>
    <Typography variant='h4'>RadioButton</Typography>
    <FormGroup row>
      <RadioButton
        name='radioButton'
        helperText='Helper text'
        label='Radio List'
        items={LIST_OF_ITEMS}
      />
      <RadioButton
        name='radioButtonInline'
        helperText='Helper text'
        label='Checkbox List Inline'
        inline
        items={LIST_OF_ITEMS}
      />
    </FormGroup>
  </>
))
