import React from 'react'
import * as Yup from 'yup'

import { Checkbox, CheckboxList } from '../../lib/'
import { Typography, FormGroup } from '../../lib/core'
import {
  Favorite,
  FavoriteBorderOutlined,
  EjectOutlined,
  EjectRounded,
} from '../../lib/icons'

import LIST_OF_ITEMS from './ListOfItems'
import { withShowCaseForm } from './withShowCaseForm'

const schema = Yup.object().shape({
  singleCheckBox: Yup.boolean(),
  customValidCheckBox: Yup.boolean(),
  chkBoxList: Yup.array().required(),
})

const initialValues = {
  singleCheckBox: false,
  customValidCheckBox: false,
  chkBoxList: [],
}

export default withShowCaseForm(initialValues, schema, () => (
  <>
    <Typography variant='h4'>Checks</Typography>
    <FormGroup row>
      <Checkbox
        name='singleCheckBox'
        label='Single Checkbox'
        helperText='Helper Text'
      />
    </FormGroup>
    <FormGroup row>
      <Checkbox
        name='customValidCheckBox'
        label='Custom Validation'
        helperText='Helper Text'
        icon={<EjectOutlined color='default' />}
        checkedIcon={<EjectRounded />}
        validate={() => 'Custom validation always invalid'}
      />
    </FormGroup>
    <FormGroup row>
      <CheckboxList
        name='chkBoxList'
        helperText='Helper text'
        label='Checkbox List'
        icon={<FavoriteBorderOutlined />}
        checkedIcon={<Favorite color='secondary' />}
        items={LIST_OF_ITEMS}
      />
    </FormGroup>
  </>
))
