import React from 'react'
import { TextFieldProps } from '@material-ui/core/TextField'

import { ControllerFieldProps } from '../types'
import { withField } from './withField'
import { useFieldProps } from './useFieldProps'
import {
  Theme,
  TextField as MUITextField,
  makeStyles,
  createStyles,
} from '../../core'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    container: {
      display: 'flex',
      flexWrap: 'wrap',
    },
    MUITextField: {
      marginLeft: theme.spacing(1),
      marginRight: theme.spacing(1),
    },
  }),
)

const ControllerTextField = (props: ControllerFieldProps<TextFieldProps>) => {
  const {
    label,
    field,
    form,
    hasError,
    errorMsg,
    blurHandler,
    setValue,
    innerRef,
    ..._props
  } = useFieldProps(props)
  const classes = useStyles({})
  const labelText = hasError ? errorMsg : label
  return (
    <MUITextField
      label={labelText}
      className={classes.MUITextField}
      margin='normal'
      variant='standard'
      error={hasError}
      {...field}
      {..._props}
    />
  )
}
export const TextField = withField(ControllerTextField)
