import { ControllerFieldProps, FormikInnerFieldProps } from '../types'
import { FormikProps } from '../..'

export function useFieldProps<T extends ControllerFieldProps<any>>(props: T) {
  const field_ = props.field as FormikInnerFieldProps
  const form_ = props.form as FormikProps<{ [key: string]: any }>
  const fieldName = field_.name
  const { touched, errors } = props.form as FormikProps<{
    [key: string]: any
  }>
  const hasError = touched[fieldName] != null && errors[fieldName] != null
  const errorMsg = errors[fieldName]
  const blurHandler = () => form_.setFieldTouched(fieldName, true)
  const setValue = (value: any) => form_.setFieldValue(fieldName, value)
  return {
    field: field_,
    form: form_,
    blurHandler,
    hasError,
    errorMsg,
    setValue,
    ...props,
  }
}
