import React from 'react'
import * as Yup from 'yup'

import { withShowCaseForm } from './withShowCaseForm'
import LIST_OF_ITEMS from './ListOfItems'

import { Switch, SwitchList } from '../../lib/'
import { Typography, FormGroup } from '../../lib/core'

const schema = Yup.object().shape({
  singleSwitch: Yup.boolean(),
  switchList: Yup.array().required(),
  customValidSwitch: Yup.boolean(),
})

const initialValues = {
  singleSwitch: false,
  customValidSwitch: false,
  switchList: [],
}

export default withShowCaseForm(initialValues, schema, () => (
  <>
    <Typography variant='h4'>Switches</Typography>
    <FormGroup row>
      <Switch
        name='singleSwitch'
        label='Basic Switch'
        helperText='Helper Text'
      />
    </FormGroup>
    <FormGroup row>
      <Switch
        name='customValidSwitch'
        label='Custom Validation'
        helperText='Helper Text'
        color='primary'
        validate={() => 'Custom validation always invalid'}
      />
    </FormGroup>
    <FormGroup row>
      <SwitchList
        name='switchList'
        helperText='Helper text'
        label='Switch List'
        items={LIST_OF_ITEMS}
      />
    </FormGroup>
  </>
))
