import React, { useEffect } from 'react'
import ThemeProvider from '@material-ui/styles/ThemeProvider'

import { ContextProvider, useContext } from '../context/'
import InitialState from '../context/initialState'
import * as Themes from './Themes'
import { getChangeBackground } from '../config'

const ThemeProviderWrapper = ({ children }: React.PropsWithChildren<{}>) => {
  const [{ config }] = useContext()

  useEffect(() => {
    if (getChangeBackground()) {
      if (config.theme.isDarkTheme) {
        document.body.style.background = '#2b2b2b'
      } else if (config.theme.isLightTheme) {
        document.body.style.background = '#f2f2f2'
      }
    }
  }, [config.theme])

  return (
    <ThemeProvider
      theme={config.theme.isDarkTheme ? Themes.Dark : Themes.Light}
    >
      {children}
    </ThemeProvider>
  )
}

export default function Provider({ children }: React.PropsWithChildren<{}>) {
  return (
    <ContextProvider initialState={InitialState}>
      <ThemeProviderWrapper>{children}</ThemeProviderWrapper>
    </ContextProvider>
  )
}

export const withProvider = (Component: React.FC<any>) => ({
  children,
  ...props
}: React.PropsWithChildren<any>) => (
  <Provider>
    <Component {...props}>{children}</Component>
  </Provider>
)
