import React from 'react'

import {
  Typography,
  makeStyles,
  createStyles,
  Card,
  CardContent,
  Box,
  Grid,
} from '../lib/core'
import { PageProps, useMouseHover } from '../lib'

const useStyles = makeStyles(
  createStyles({
    card: {
      boxShadow: '0',
    },
    cardRaised: {
      boxShadow: '3',
    },
  }),
)

const CardItem = ({
  title,
  content,
  onClick,
}: {
  title: string
  content: string
  onClick: () => void
}) => {
  const classes = useStyles({})
  const [mouseHover, mouseHoverHandler] = useMouseHover()

  return (
    <Grid item xs={3}>
      <Card
        className={classes.card}
        raised={mouseHover}
        {...mouseHoverHandler}
        onClick={onClick}
      >
        <CardContent>
          <Typography variant='h6'>{title}</Typography>
          <Typography variant='body2' component='p'>
            {content}
          </Typography>
        </CardContent>
      </Card>
    </Grid>
  )
}

export default ({ navigate }: PageProps) => {
  return (
    <>
      <Box paddingX={5} marginTop={5}>
        <Typography variant='h3' color='textSecondary'>
          Form Showcase
        </Typography>
      </Box>
      <Box padding={5}>
        <Grid container spacing={2}>
          <CardItem
            title='TextField'
            content='Text input, textarea, datepicker, number'
            onClick={() => (navigate as any)('textfield')}
          />
          <CardItem
            title='Checks'
            content='Checkbox, CheckboxList'
            onClick={() => (navigate as any)('checks')}
          />
          <CardItem
            title='Switches'
            content='Switch, SwitchList'
            onClick={() => (navigate as any)('switches')}
          />
          <CardItem
            title='RadioButton'
            content='Just a RadioButton'
            onClick={() => (navigate as any)('radio')}
          />
          <CardItem
            title='Selections'
            content='Select, Autocomplete, Multi Select'
            onClick={() => (navigate as any)('selections')}
          />
        </Grid>
      </Box>
    </>
  )
}
